<template>
    <div class="main-pocket">
        
        <div 
        v-if="actualPocket.pocketImage"
        class="pocket-div-image">

            <img
            :src="getImage"
            alt="image"
            class="pocket-image"
            >

            <div
            v-if="actualPocket.leftArrow"
            @click="moveLeft"
            class="pocket-left-arrow">
                <img
                :src="getImageLeft"
                alt="moveLeft">
            </div>

            <div
            v-if="actualPocket.rightArrow"
            @click="moveRight"
            class="pocket-right-arrow">
                <img
                :src="getImageRight"
                alt="moveRight">
            </div>
        </div>        

    </div>
</template>

<script>

export default {
    props: {
        actualPocket: {}
    },
    methods: {
        moveLeft(){
            this.$emit("action", "previous")
        },
        moveRight(){
            this.$emit("action", "next")
        }
    },
    computed: {
        getImage() {
            return require(`@/images/${this.actualPocket.pocketImage}`)
        },
        getImageLeft(){
            return require("@/images/leftarrow.png")
        },
        getImageRight(){
            return require("@/images/rightarrow.png")
        }        
    }
}

</script>

