<template>
    <div class="main-body">

        <BodyImage 
            v-if="actualBody.bodyImage"
            :bodyImage="actualBody.bodyImage"
            class="main-body-div-image"
        />
        <div class="main-body-textanswers">
            <BodyText
                v-if="actualBody.bodyText"
                :bodyText="actualBody.bodyText"
                class="main-body-text"
            />
            <BodyQuestion
                v-if="actualBody.bodyQuestion"
                :bodyQuestion="actualBody.bodyQuestion"
                @action="actionTrueAnswer($event)"
            />
        </div>
    </div>
</template>

<script>
import BodyImage from '@/components/BodyImage.vue'
import BodyText from '@/components/BodyText.vue'
import BodyQuestion from '@/components/BodyQuestion.vue'


export default {
    components: {
        BodyImage,
        BodyText,
        BodyQuestion
    },
    props: {
        actualBody: {}
    },
    methods: {
        actionTrueAnswer(action){
            this.$emit("action", action)
        }
    }
}

</script>

