<template>
    <div>
        <img
            :src="getImage"
            alt="image"
            class="main-body-image"
            >
    </div>
</template>

<script>
export default {
    props: {
        bodyImage: {}
    },
    computed: {
        getImage() {
            return require(`@/images/${this.bodyImage}`)
        }
    }
    
}

</script>

