<template>
    <div class="error-modal">
        
        <div class="error-modal-body">
            <div class="error-modal-body-text">
                {{errorText}}
            </div>
            <div
            @click="closeModal"
            class="error-button-ok">
                <div class="error-button-ok-text">
                    OK
                </div>
            </div>
        </div>        
    </div>
</template>

<script>
export default {
    props: {
        errorText: String
    },
    methods: {
        closeModal(){
            this.$emit("close")
        }
    }
    
}

</script>

