<template>
    <div @click="buttonPush(button.action)" class="footer-button">
        <div class="footer-button-text">
            {{button.buttonText}}
        </div>
    </div>
</template>

<script>
export default {
    props: {
        button: {}
    },
    methods: {
        buttonPush(action){
            this.$emit("action", action)
        }
    }
    
}

</script>

