<template>
  <div class="main-wrapper">
    <div class="main">
      <MainHeader
        v-if="actualState.header"
        :actualState = actualState
      />
      <MainBody
        v-if="actualState.body"
        :actualBody = actualState.body
        @action = doAction($event)
      />
      <MainPocket
        v-if="actualState.pocket"
        :actualPocket = actualState.pocket
        @action = doAction($event)
      />
      <MainFooter
        v-if="actualState.footer"
        :actualFooter = actualState.footer
        @action = doAction($event)
      />
    </div>
  </div>
</template>

<script>
import MainHeader from '@/components/MainHeader.vue'
import MainBody from '@/components/MainBody.vue'
import MainPocket from '@/components/MainPocket.vue'
import MainFooter from '@/components/MainFooter.vue'
import MainData from '@/data/data.json'

export default {
  name: 'App',
  data() {
    return {
      mainBranch: MainData,
      stepNumber: 0,
      actualState: {},      
    }
  },
  components: {
    MainHeader,
    MainBody,
    MainPocket,
    MainFooter  
  },
  methods: {
    doAction(action){
      if ((action === "next")&&(this.stepNumber < this.mainBranch.length-1)) {
        this.stepNumber += 1
        this.actualState = this.mainBranch[this.stepNumber]
      }
      if ((action === "previous")&&(this.stepNumber > 0)) {
        this.stepNumber -= 1
        this.actualState = this.mainBranch[this.stepNumber]
      }
      if (action === "replay") {
        this.stepNumber = 0
        this.actualState = this.mainBranch[this.stepNumber]
      }    
    }
  },
  created() {
    this.actualState = this.mainBranch[this.stepNumber]
  }
}
</script>

