<template>
    <div class="main-body-question">
        <div class="main-body-question-text">
            {{bodyQuestion.questionText}}
        </div>
        
        
        <AnswerButton
        v-for="(answer, index) in bodyQuestion.answers" :key="index"
        :answer = answer
        @actiontrue="actionTrueAnswer($event)"
        @actionfalse="actionFalseAnswer($event)"
        />
            
        <FalseAnswer
        v-if="modalFalseOpen"
        :errorText = "errorText"
        @close="closeModal"
        />

        <TrueAnswer
        v-if="modalTrueOpen"
        />
        
    </div>
</template>

<script>
import AnswerButton from '@/components/AnswerButton.vue'
import FalseAnswer from '@/components/FalseAnswer.vue'
import TrueAnswer from '@/components/TrueAnswer.vue'

export default {
    data() {
        return {
        modalFalseOpen: false,
        modalTrueOpen: false,
        errorText: ""
        }
    },
    components: {
        AnswerButton,
        FalseAnswer,
        TrueAnswer
    },
    props: {
        bodyQuestion: {}
    },
    methods: {
        async actionTrueAnswer(action){
            this.modalTrueOpen = true
            await this.sleep(600)
            this.modalTrueOpen = false
            this.$emit("action", action)
        },
        // actionTrueAnswer(action){
        //     this.modalTrueOpen = true
        //     this.showMessageTrue(()=> {
        //         this.modalTrueOpen = false
        //         this.$emit("action", action)
        //     }            
        //     )
        // },
        // async showMessageTrue(callback) {
        //     this.modalTrueOpen = true     
        //     await this.sleep(1000)
        //     callback()
        // },
        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms))
        },

        actionFalseAnswer(action){
            this.modalFalseOpen = true
            this.errorText = action
        },
        closeModal(){
            this.modalFalseOpen = false
        }        
    }    
}

</script>

