<template>
    <div
    @click="checkAnswer"
    class="answer-button">
        <div class="answer-text">
            {{answer.answerText}}
        </div>
    </div>
</template>

<script>
export default {
    props: {
        answer: {}
    },
    methods: {
        checkAnswer(){
            if (this.answer.action === "falseAnswer") {
                this.$emit("actionfalse", this.answer.errorText)
            }
            else if (this.answer.action === "trueAnswerNext") {
                this.$emit("actiontrue", "next")
            }
        }
    }
    
}

</script>

