<template>
    <div class="main-footer">
        <FooterButton 
        v-for="(button, index) in actualFooter" :key="index"
        :button = button
        @action="actionButton($event)"
        />
    </div>
</template>

<script>
import FooterButton from '@/components/FooterButton.vue'

export default {
    components: { FooterButton },
    props: {
        actualFooter: []
    },
    methods: {
        actionButton(action){
            this.$emit("action", action)
        }
    }
    
}

</script>

